@media screen and (min-width: 320px) {
	.App {
	  position: relative;
	  width: 414px;
	  left: 50%;
	  margin-left: -207px;
	  margin-bottom: 30px;
	}
}

@media screen and (min-width: 480px) {
	.App {
	  position: relative;
	  width: 1024px;
	  left: 50%;
	  margin-left: -512px;
	  margin-bottom: 30px;
	}
}

section, container {
  display: block;
}

.app-overlay {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	z-index: 100;
	background-color: black;
	opacity: 0.6;
}


