.footer {
	position: relative;
	overflow: auto;
	margin-top: 157px;
	font-family: Roboto-Light, sans-serif;
	font-size: 16px;
	color: #3c3f45;
	word-wrap: no-wrap;
}

@media screen and (min-width: 320px) {
	.footer {
		margin-left: 73px;
		margin-top: 80px;
	}

	.footer img {
		width: 32px;
		margin-top: 20px;
		margin-right: 20px;
	}
}

@media screen and (min-width: 480px) {
	.footer {
		margin: 0px;
	}

	.footer container {
		display: inline-block;
		vertical-align: middle;
		margin: 0px 60px;
	}

	.footer img {
		width: 57px;
		margin: 0px 5px;
	}
}

.footer a {
	font-family: Roboto, sans-serif;
	font-size: 16px;
	color: #3c3f45;
}

.footer p {
	margin: 0px;
}

.footer .copyright {
	font-weight: bold;
}


