.mainContent .welcome {
	position: relative;
	max-width: 1023px;
	margin-top: -40px;
}

@media screen and (min-width: 480px) {
	.mainContent .welcome {
		height: 689px;
	}
}

.mainContent .welcome .hero {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
}

@media screen and (min-width: 320px) {
	.mainContent .welcome h1 {
		position: relative;
		left: 76px;
		top: 30px;
		width: 249px;
		height: 252px;
		color: white;
		font-family: Roboto-Light, sans-serif;
		font-size: 42px;
		font-weight: normal;
	}
}

@media screen and (min-width: 480px) {
	.mainContent .welcome h1 {
		position: relative;
		left: 177px;
		top: 240px;
		width: 728px;
		height: 281px;
		color: white;
		font-family: Roboto-Light, sans-serif;
		font-size: 83px;
		font-weight: normal;
	}
}

@media screen and (min-width: 320px) {
	.mainContent .buttonPanel {
		margin-top: 105px;
		text-align: center;
	}
}

@media screen and (min-width: 480px) {
	.mainContent .buttonPanel {
		margin-top: 95px;
		text-align: center;
	}
}

.mainContent .buttonPanel input {
	width: 323px;
	height: 61px;
	text-align: center;
	font-family: Roboto-Light, sans-serif;
	font-size: 20px;
	color: #3c3f44;
	background-color: transparent;
	border: 1px solid #3c3f44;
	margin: 10px;
}

.mainContent .buttonPanel a {
	display: block;
	font-size: 18px;
	margin-top: 40px;
	color: #3c3f45;
}


