@media screen and (min-width: 480px) {
	.email-form {
		display: block;
		position: absolute;
		width: 400px;
		top: 100px;
		left: 50%;
		margin-left: -200px;
		border: 1px solid black;
		-webkit-box-shadow: 4px 4px 10px #4d433f;
		        box-shadow: 4px 4px 10px #4d433f;
		background-color: white;
		font-family: Roboto-Light, sans-serif;
		z-index: 101;
	}

	.email-form .heading {
		display: block;
		height: 50px;
		background-color: #d4501d;
		color: white;
		font-weight: bold;
	}

	.email-form .heading img {
		width: 40px;
		margin: 5px;
	}

	.email-form .message {
		margin: 10px;
		font-size: 22px;
	}

	.email-form fieldset {
		margin: 10px;
		padding-top: 20px;
	}

	.email-form fieldset label {
		display: block;
		position: relative;
		margin-left: 20px;
		margin-bottom: 10px;
		font-size: 20px;
		font-weight: bold;
		width: 315px;
		height: 50px;
	}

	.email-form fieldset label input {
		position: absolute;
		right: 5px;
		width: 220px;
		height: 40px;
		font-size: 22px;
	}

	.email-form fieldset input[type=submit] {
		width: 240px;
		height: 61px;
		text-align: center;
		font-family: Roboto-Light, sans-serif;
		font-size: 20px;
		font-weight: bold;
		color: #3c3f44;
		background-color: transparent;
		border: 1px solid #3c3f44;
		margin: 10px;
	}
}

@media screen and (min-width: 320px) {
	.email-form {
		display: block;
		position: absolute;
		width: 320px;
		top: 100px;
		left: 50%;
		margin-left: -160px;
		border: 1px solid black;
		-webkit-box-shadow: 4px 4px 10px #4d433f;
		        box-shadow: 4px 4px 10px #4d433f;
		background-color: white;
		font-family: Roboto-Light, sans-serif;
		z-index: 101;
	}

	.email-form .heading {
		display: block;
		height: 50px;
		background-color: #d4501d;
		color: white;
		font-weight: bold;
	}

	.email-form .heading img {
		width: 40px;
		margin: 5px;
	}

	.email-form .message {
		margin: 10px;
		font-size: 22px;
	}

	.email-form fieldset {
		margin: 10px;
		padding-top: 20px;
	}

	.email-form fieldset label {
		display: block;
		position: relative;
		margin-left: 20px;
		margin-bottom: 10px;
		font-size: 18px;
		font-weight: bold;
		width: 235px;
		height: 50px;
	}

	.email-form fieldset label input {
		position: absolute;
		right: 5px;
		width: 150px;
		height: 36px;
		font-size: 18px;
	}

	.email-form fieldset input[type=button] {
		width: 230px;
		height: 50px;
		text-align: center;
		font-size: 18px;
		font-weight: bold;
		color: #3c3f44;
		background-color: transparent;
		border: 1px solid #3c3f44;
		margin: 10px 20px;
	}
}


