@media screen and (min-width: 320px) {
	.header {
		padding: 20px;
	}

	.header .rightSide {
		padding: 10px;
		float: right;
	}

	.header .rightSide img {
		width: 34px;
		margin-left: 8px;
	}
}

@media screen and (min-width: 480px) {
	.header {
		padding: 20px;
	}

	.header .rightSide {
		padding: 10px;
		float: right;
	}

	.header .rightSide img {
		width: 34px;
	}
}


